import React from 'react'
import './portfolio.css'
import Ppm from '../../assets/ppm.png'
import Bim from '../../assets/bim.png'
import Gratyo from '../../assets/gratyo.png'
// import Kartbes from '../../assets/kartbes.png'
// import Interbat from '../../assets/interbat.png'
import Zambon from '../../assets/zambon.png'
import Ff from '../../assets/ff.png'
// import imgComponent from '../imgComponent'

const data = [
  {
    id:1,
    image: Ff,
    title: "Cashflow Web Applications"
  },
  {
    id:2,
    image: Bim,
    title: "Building Information Modeling Management System"
  },
  {
    id:3,
    image: Ppm,
    title: "Project Management System"
  },
  {
    id:4,
    image: Gratyo,
    title: "Coaching Management System"
  },
  {
    id:5,
    image: Zambon,
    title: "Zambon Management System"
  },
]

const Portfolio = () => {
  return (
    <section id='portfolio'>Portfolio
    <h5>My Recent Work</h5>
    <h2>Portfolio</h2>

    <div className="container portfolio__container">
      {
        data.map(({id,image,title}) => {
          return (
            <article key={id} className='portfolio__items'>
            <div className="portfolio__item-image">          
            <img src={image} alt='Latest Project At KPN Corp'/>
            </div>
            <h3>{title}</h3>
          </article>
          )
        })
      }
    </div>
    </section>
  )
}

export default Portfolio