import React from 'react'
import './footer.css'
import {BsLinkedin} from 'react-icons/bs'
import {FaInstagram} from 'react-icons/fa'

const Footer = () => {
  return (
    <footer>
      <a href='#' className='footer__logo'>PUTRA</a>
      <ul className='permalinks'>
        <li><a href="#">Home</a></li>
        <li><a href="#about">About</a></li>
        <li><a href="#exp">Experience</a></li>
        <li><a href="#portfolio">Portfolio</a></li>
        <li><a href="#contact">Contact</a></li>
      </ul>
      <div className="footer__socials">
        <a href="https://www.linkedin.com/in/tputraa/" target="_blank"><BsLinkedin/></a>
        <a href="https://www.instagram.com/tputra.a/" target="_blank"><FaInstagram/></a>
      </div>
      <div className="footer__cp">
        <small>&copy; PUTRA. All Rights Reserved.</small>
      </div>
    </footer>
  )
}

export default Footer