import React from 'react'
import './exp.css'
import {BsFillPatchCheckFill} from 'react-icons/bs'

const Exp = () => {
  return (
    <section id='exp'>Experience
    <h5>Skills</h5>
    <h2>My Experience</h2>

    <div className="container exp__container">
       <div className="exp__fe">
         {/* FE */}
         <h3>Frontend Development</h3>
         <div className="exp__content">
           <article className='exp__details'>
            <BsFillPatchCheckFill className='exp__details-icon'/>
              <div>
                <h4>HTML</h4>
                <small className='text-light'>Experienced</small>
              </div>
           </article>
           <article className='exp__details'>
            <BsFillPatchCheckFill className='exp__details-icon'/>
              <div>
                <h4>CSS</h4>
                <small className='text-light'>Intermediate</small>
              </div>
           </article>
           <article className='exp__details'>
            <BsFillPatchCheckFill className='exp__details-icon'/>
              <div>
                <h4>JavaScript</h4>
                <small className='text-light'>Intermediate</small>
              </div>
           </article>
           <article className='exp__details'>
            <BsFillPatchCheckFill className='exp__details-icon'/>
              <div>
                <h4>Bootstrap</h4>
                <small className='text-light'>Experienced</small>
              </div>
           </article>
           <article className='exp__details'>
            <BsFillPatchCheckFill className='exp__details-icon'/>
              <div>
                <h4>React</h4>
                <small className='text-light'>Still Learning...</small>
              </div>
           </article>
         </div>
        </div> 
        {/* BE */}
       <div className="exp__be">
       <h3>Backend Development</h3>
       <div className="exp__content">
           <article className='exp__details'>
            <BsFillPatchCheckFill className='exp__details-icon'/>
              <div>
                <h4>PHP</h4>
                <small className='text-light'>Intermediate</small>
              </div>
           </article>
           <article className='exp__details'>
            <BsFillPatchCheckFill className='exp__details-icon'/>
              <div>
                <h4>MySQL</h4>
                <small className='text-light'>Experienced</small>
              </div>
           </article>
           <article className='exp__details'>
            <BsFillPatchCheckFill className='exp__details-icon'/>
              <div>
                <h4>MSSQL</h4>
                <small className='text-light'>Intermediate</small>    
              </div>
           </article>
           <article className='exp__details'>
            <BsFillPatchCheckFill className='exp__details-icon'/>
              <div>
                <h4>PostgreSQL</h4>
                <small className='text-light'>Intermediate</small>
              </div>
           </article>
           <article className='exp__details'>
            <BsFillPatchCheckFill className='exp__details-icon'/>
              <div>
                <h4>NodeJs</h4>
                <small className='text-light'>Still Learning...</small>
              </div>
           </article>
        </div>
       </div>
    </div>
    </section>
  )
}

export default Exp