import React from 'react'
import './about.css'
import {BsFillCalendarWeekFill} from 'react-icons/bs'
import {BsFillCalendarRangeFill} from 'react-icons/bs'
import Me from '../../assets/nm.jpeg'

const About = () => {
  return (
    <section id='about'>About
    <h5>Get To Know</h5>
    <h2>About Me</h2>

    <div className="container about__container">
      <div className="about__me">
        <div className="about__me-image">
          <img src={Me} alt='Me'/>
        </div>
      </div>

    <div className="about__content">
        <div className="about__cards">
          <article className='about__card'>
            <BsFillCalendarWeekFill className='about__icon'/>
            <h5>2 Years Working on Haribima IT Consultant</h5>
            <small></small>
          </article>

          <article className='about__card'>
            <BsFillCalendarRangeFill className='about__icon'/>
            <h5>2 Years Working on KPN Corp</h5>
            <small></small>
          </article>
        </div>
        <p>
          Hello! I'm Putra, a web developer based in Jakarta, who enjoys building things that live on the internet. I develop exceptional websites and web apps that provide intuitive, data integrated with efficient and modern backends.
          Shortly after graduating from STTI I - Tech, I joined the back-end team at Haribima IT Consultant where I work on a wide variety of interesting and meaningful projects on a daily basis.
          </p>
          <a href='#contact' className='btn btn-primary'>Let's Talk</a>
      </div>
    </div>
    </section>
  )
}

export default About